import { graphql, PageProps } from "gatsby";

import { useTranslation } from "gatsby-plugin-react-i18next";

import Layout from "../../components/layout";
import Seo from "../../components/seo";
import Workshops from "../../components/pages/workshops";

import useEditionData from "../../hooks/useEditionData";
import React from "react";

const WorkshopsPage: React.FC<PageProps> = ({ location }) => {
  const { t } = useTranslation();

  const editionData = useEditionData(2022);

  return (
    <Layout>
      <Seo title={t("title")} pathname={location.pathname} />

      <Workshops editionData={editionData} />
    </Layout>
  );
};

export default WorkshopsPage;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(
      filter: {
        ns: { in: ["common", "workshops"] }
        language: { eq: $language }
      }
    ) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
